/* navbar */
.navbar-brand {
  padding: 0px;
}
.navbar-brand>img {
  height: 100%;
  padding: 5px;
  width: auto;
}

.modify-nav .navbar-brand {
  height: 80px;
}

.modify-nav .nav >li >a {
  padding-top: 30px;
  padding-bottom: 30px;
}
.modify-nav .navbar-toggle {
  padding: 10px;
  margin: 25px 15px 25px 0;
}

.modify-nav .navbar-toggle .icon-bar {
  background-color: black;
}

.dropdown-submenu {
  position: relative;
}

.dropdown-submenu>.dropdown-menu {
  top: 0;
  left: 100%;
  margin-top: -6px;
  margin-left: -10px;
  -webkit-border-radius: 0 6px 6px 6px;
  -moz-border-radius: 0 6px 6px;
  border-radius: 0 6px 6px 6px;
}

.dropdown-submenu>.dropdown-menu>li {
  font-size: smaller;
  -webkit-text-size-adjust: none;
}

.dropdown-submenu:hover>.dropdown-menu {
  display: block;
}

.dropdown-submenu.pull-left {
  float: none;
}

.dropdown-submenu.pull-left>.dropdown-menu {
  left: -100%;
  margin-left: 10px;
  -webkit-border-radius: 6px 0 6px 6px;
  -moz-border-radius: 6px 0 6px 6px;
  border-radius: 6px 0 6px 6px;
}

.subdropdown-right {
  top: 50%; right: 90%; left: auto;
}



/**** home ****/

/* 輪播牆 */
.carousel-fade {
  .carousel-inner {
    .item {
      transition-property: opacity;
    }

    .item,
    .active.left,
    .active.right {
      opacity: 0;
    }

    .active,
    .next.left,
    .prev.right {
      opacity: 1;
    }

    .next,
    .prev,
    .active.left,
    .active.right {
      left: 0;
      transform: translate3d(0, 0, 0);
    }
  }

  .carousel-control {
    z-index: 2;
  }
}

/* 首頁內容 */
.h1-responsive {
  font-size: 20px;
}

@media (min-width: 500px){
  .h1-responsive {
    font-size: 24px;
  }
}

@media (min-width: 768px){
  .h1-responsive {
    font-size: 28px;
  }
}

@media (min-width: 992px){
  .h1-responsive {
    font-size: 32px;
  }
}



.carousel, .carousel-inner, .carousel-inner .item {
  height: 100%;
}

